/* Empty. Add your own CSS if you like */
@import "variables";
@import "mixins";
$primarycolor: #fff;
$esatcolor: #a7a9ac;
$white:#fff;
$buttoncolor:#7192b0;
$buttonhovercolor:#8fbc8f;
$font-color:#666;
$esatfontpath: "../fonts" !default;
$esat-version: "1.0.1" !default;
$max-font-size:2.1em;
$mid-font-size:1.7em;
$error-color:red;
$roboto-thin-font-family: 'Roboto-Thin-webfont';
@font-face {
 font-family:$roboto-thin-font-family;
 src:url("#{$esatfontpath}/Roboto-Thin-webfont.eot?v=#{$esat-version}");
 src:url("#{$esatfontpath}/Roboto-Thin-webfont.eot?v=#{$esat-version}#iefix") format("embedded-opentype"),
  url("#{$esatfontpath}/Roboto-Thin-webfont.ttf?v=#{$esat-version}") format("truetype"),
  url("#{$esatfontpath}/Roboto-Thin-webfont.woff?v=#{$esat-version}") format("woff"),
  url("#{$esatfontpath}/Roboto-Thin-webfont.woff") format("woff"), /* for WP8 */
  url("#{$esatfontpath}/Roboto-Thin-webfont.svg?v=#{$esat-version}#Ionicons") format("svg");
 font-weight: normal;
 font-style: normal;
}
