      color:$white;
    }

   .Symbol-alarm2 {
    color: #fff;
    font-size: 23px;
    text-align: center;
    width: 43px;
    margin: 5px 0px;
}
}

.tabs {
  height:70px;
  background: $esatcolor;
  color:$white;
}

//view Container
.view-container{
	background: $primarycolor;
  .scroll-content {
    position: relative;
  }

   .pane{
	    background: $primarycolor !important;
	   .list {
		  position: relative;
		  display: block;
		  margin-top: 30px;
       }
	   
	   .mainli{
			margin:30px!important;
      @include item-style(#444, #333, #acc);
		}
		.liitem{
		   display:inline-block !important;
		   width:49% !important;
		   margin:0 !important;
       border:0px;
       i{
         display:block;
         font-size:5em;
       }
		}
		.liitem a{
			line-height:50px;
			text-align:center;
		}
	  }
	 .customsegment{
        margin: 20px auto;
      width: 70% !important;
  }
   [nav-view-transition="ios"][nav-view-direction="forward"], [nav-view-transition="ios"][nav-view-direction="back"] {
    background-color:$white !important;
}
.logo{
  h2{
    font-family:$roboto-thin-font-family;
    font-size:$max-font-size;
    color:$font-color;
    display:block;
    font-weight: 500;
    text-align: center;
  }
  h3{
      font-family:$roboto-thin-font-family;
      font-size:$mid-font-size;
      color:$font-color;
      display:block;
      font-weight: 400;
      text-align: center;
  }
 
}


//swiper
.swiper-slide {
    width:100%;
    height:100px !important;
}
.loginWrapper{
    background:$esatcolor;
	  max-width: 352px;
    height: 407px;
    display: block;
    margin: 0 auto;
    padding: 30px;
    .item-input{
      padding: 6px 0 5px 0;
    }
.error{
  color:$error-color;
  font-size:$font-size-large;
}
 figure {
  position: relative;
  width: 10em;
  height: 10em;
  margin: 0 auto;
  margin-bottom: 10px;
  border: 0;
  box-shadow: none;
  border: 0;
  box-shadow: none;
  outline: 0;
  background: #a7a9ac;
  img {
    transition: all ease-in-out 0.5s;
    border: 0;
    box-shadow: none;
    width: 100%;
    height: 100%;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,);
    border: 0;
    box-shadow: none;
  }
  svg {
    fill: #666;
    width: 80px;
    opacity: 0;
    transition: all ease-in-out 0.5s;
    margin: 0 auto;
    position: absolute;
    left: 15%;
    top: 0;
    z-index: 9;
  }
  &:hover {
    svg {
      width: 80px;
      display: block;
      transition: all ease-in-out 0.5s;
      opacity: 1;
      animation: zoomIn 1s;
    }
    img {
      opacity: 0;
    }
    .FINGERPATH {
      stroke: red;
      stroke-dasharray: 1000;
      stroke-dashoffset: 1000;
      animation: dash 5s linear alternate infinite;
    }
  }
}

@keyframes dash {
  from {
    stroke-dashoffset: 1000;
  }

  to {
    stroke-dashoffset: 0;
  }
}
.item{
  background: none;
  border:0px;
}
input{
      background:$white;
    padding: 20px;
}
.button{
    background: $buttoncolor;
  border: 1px solid #bbb;
  position: relative;
  padding: 10px;
  line-height:26px;
&.button-positive{

  background: $buttoncolor;
  border: 1px solid #bbb;
  position: relative;
  padding: 10px;
}
&:hover, &:focus, &:active{
  background: $buttonhovercolor;
  border: 1px solid #bbb;

}
}

   
}




